import { arrayFind } from '@utils/util';

// $type 
//  inputParametersnodeType(userTask)(monitorTask)
// businessObjectgetNodeType

export function getExtensionElement(element, type) {
  if (!element.extensionElements) {
    return;
  }
  return element.extensionElements.values.filter((extensionElement) => {
    return extensionElement.$instanceOf(type);
  })[0];
}

// 
// -- userTask  monitorTask
// -- notificationTask
// 
export function getNodeType(element) {
  let tempNodeType = 'userTask';
  if (element.shapeFlag) {
    tempNodeType = element.shapeFlag;
  }
  const businessObject = element.businessObject;
  if (businessObject && businessObject.extensionElements) {
    const params = getExtensionElement(businessObject, 'camunda:inputOutput');
    if (params && params.$children) {
      const nodeTypeObj = arrayFind(params.$children, 'name', 'nodeType');
      if (nodeTypeObj) {
        tempNodeType = nodeTypeObj.$body;
      }
    }
  }
  return tempNodeType;
}
