
























































































import { Component, Vue } from 'vue-property-decorator';
import paramsCache from '@mixins/paramCache';
import {
  processQuery,
  tempPublish,
  tempCopy,
  tempValidate,
  tempDelete,
  tempUpload,
  tempExport
} from '@api/processConfig';
import { arrayFind } from '@utils/util';
import FlowChartPreview from '@views/processAssignment/components/flowChartPreview.vue';

import { namespace } from 'vuex-class';
const pageTemplate = namespace('processTemplate');

Component.registerHooks(['beforeRouteLeave']);
const paramsCacheMixin = paramsCache([
  'name',
  'page',
  'limit',
  'processList',
  'total'
]);

@Component({
  components: { FlowChartPreview },
  mixins: [paramsCacheMixin]
})
export default class ProcessConfig extends Vue {
  @pageTemplate.State('pageFresh') pageFresh; // 

  private dialogVisible = false;
  private importDialogVisible = false;
  private upLoad = false;
  private importFiles: Array<any> = [];
  private processList: Array<any> = [];
  private name = '';
  private loading = false;
  private tipShow = false;
  private page = 1;
  private limit = 10;
  private total = 0;
  private currentTempId = '';

  columns() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '',
        width: '60'
      },
      {
        prop: 'flowName',
        label: '',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '160'
      },
      {
        prop: 'status',
        'header-align': 'left',
        label: '',
        showOverflowTooltip: true,
        'min-width': '60',
        formatter: _this.statusTypeFormatter
      },
      {
        prop: 'updatedBy',
        'header-align': 'left',
        label: '',
        showOverflowTooltip: true,
        'min-width': '80'
      },
      {
        prop: 'updatedTime',
        'header-align': 'left',
        label: '',
        showOverflowTooltip: true,
        'min-width': '120'
      },
      {
        prop: '',
        align: 'center',
        label: '',
        'min-width': '360',
        headerAlign: 'center',
        actions: [
          {
            code: 'preview',
            text: '',
            onClick: _this.previewTemp
          },
          {
            code: 'edit',
            text: '',
            onClick: _this.editTemp
          },
          {
            code: 'copy',
            text: '',
            onClick: _this.copyTemp
          },
          {
            code: 'publish',
            text: '',
            disabled: _this.publishCheck,
            onClick: _this.publishTemp
          },
          {
            code: 'validate',
            text: '',
            onClick: _this.validateTemp
          },
          {
            code: 'delete',
            text: '',
            onClick: _this.deleteTemp
          },
          {
            code: 'export',
            text: '',
            onClick: _this.exportTemp
          }
        ]
      }
    ];
  }
  private statusTypes: Array<any> = [
    {
      text: '',
      value: 0
    },
    {
      text: '',
      value: 1
    },
    {
      text: '',
      value: 2
    }
  ];
  titleBtns() {
    return [
      {
        title: '',
        icon: 'icon_upload',
        onClick: this.uploadTemp
      },
      {
        title: '',
        icon: 'add-line',
        onClick: this.createTemp
      }
    ];
  }
  statusTypeFormatter(
    row: object,
    column: object,
    cellValue: number
    // index: number
  ) {
    return arrayFind(this.statusTypes, 'value', cellValue).text;
  }

  doUpload() {
    if (this.importFiles.length === 0) {
      this.$message.error('');
    } else {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.loading = true;
          this.upLoad = true;
          const uploadForm = new FormData();
          this.importFiles.forEach((item) =>
            uploadForm.append('file', item.raw)
          );
          this.importFiles.forEach((item) =>
            uploadForm.append('fileName', item.name)
          );
          tempUpload(uploadForm).then((res) => {
            if (!res.error) {
              this.upLoad = false;
              this.$message.success('');
              this.getProcessList(1);
            } else {
              this.$message.error(`${res.error.message}`);
            }
            this.loading = false;
            this.importDialogVisible = !this.importDialogVisible;
          });
        })
        .catch(() => {});
    }
  }

  uploadTemp() {
    this.importFiles = [];
    this.importDialogVisible = !this.importDialogVisible;
  }

  closeDialog() {
    const nu: any = this.$refs.netUpload;
    nu.clearFiles();
    this.importDialogVisible = !this.importDialogVisible;
    this.loading = false;
    this.upLoad = false;
  }

  // 
  handleUploadFileChange(file, fileList) {
    this.importFiles = fileList;
  }

  // 
  onRemove(file, fileList) {
    this.importFiles = fileList;
  }
  exportTemp(record) {
    tempExport(record.id);
  }
  deleteTemp(record) {
    this.$confirm('' + record.flowName + '?', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
      .then(() => {
        tempDelete({ id: record.id }).then(({ data, error }) => {
          if (!error) {
            this.$message.success('');
            this.getProcessList(this.page);
          } else {
            this.$message.error(error.message);
          }
        });
      })
      .catch(() => {});
  }
  editTemp(record) {
    this.$router.push({
      path: '/processCenter/processNode',
      query: { flag: '1', id: record.id }
    });
  }
  createTemp() {
    this.$router.push({
      path: '/processCenter/processNode',
      query: { flag: '2' }
    });
  }

  previewTemp(record) {
    this.currentTempId = record.id;
    this.dialogVisible = true;
  }

  cancelDialog() {
    this.dialogVisible = false;
  }

  copyTemp(record) {
    tempCopy({ id: record.id }).then(({ data, error }) => {
      if (!error) {
        this.$router.push({
          path: '/processCenter/processNode',
          query: { flag: '1', id: data.id }
        });
      } else {
        this.$message.error(error.message);
      }
    });
  }

  validateTemp(record) {
    tempValidate({ id: record.id }).then(({ data, error }) => {
      if (!error) {
        let innerHtml = `<p>${data.errorCount}${data.warningCount}</p>`;
        if (data.errorCount !== 0 || data.warningCount !== 0) {
          let errorInfo = '';
          if (
            data['errorValidateResults'] &&
            data['errorValidateResults'].length
          ) {
            data['errorValidateResults'].forEach((item) => {
              errorInfo = `${errorInfo}${item.message}<br>`;
            });
          }
          let warningInfo = '';
          if (
            data['warningValidateResults'] &&
            data['warningValidateResults'].length
          ) {
            data['warningValidateResults'].forEach((item) => {
              warningInfo = `${warningInfo}${item.message}<br>`;
            });
          }
          if (errorInfo !== '') {
            innerHtml = `${innerHtml}<p></p><p>${errorInfo}</p>`;
          }
          if (warningInfo !== '') {
            innerHtml = `${innerHtml}<p></p><p>${warningInfo}</p>`;
          }
          this.$alert(innerHtml, '', {
            dangerouslyUseHTMLString: true,
            type: 'error'
          });
        } else {
          innerHtml = `${innerHtml}`;
          this.$alert(innerHtml, '', {
            dangerouslyUseHTMLString: true,
            type: 'success'
          });
        }
      } else {
        this.$message.error(error.message);
      }
    });
  }
  publishTemp(record) {
    this.$confirm('' + record.flowName + '?', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
      .then(() => {
        tempPublish({ id: record.id }).then(({ error }) => {
          if (!error) {
            this.$message.success('');
            this.getProcessList(this.page);
          } else {
            this.$message.error(error.message);
          }
        });
      })
      .catch(() => {});
  }
  publishCheck(record) {
    return record.status === 1;
  }
  handleSize(val) {
    this.page = 1;
    this.limit = val;
    this.getProcessList(this.page);
  }
  handleCurrent(val) {
    this.page = val;
    this.getProcessList(this.page);
  }
  getProcessList(page: number) {
    this.page = page;
    const obj = {
      page: this.page,
      limit: this.limit,
      queryStr: this.name
    };
    this.loading = true;
    processQuery(obj).then(({ data, error }) => {
      this.loading = false;
      if (!error) {
        this.processList = data.rows;
        this.total = data.total;
      } else {
        this.$message.error(error.message);
      }
    });
  }

  created() {
    // 
    if (this.pageFresh) {
      this.getProcessList(this.page);
      this.$store.dispatch('processTemplate/pageFresh');
    }
    // 
    !this.processList.length && this.getProcessList(this.page);
    this.$store.dispatch('processTemplate/getallFieldsData');
  }

  beforeRouteLeave(to, from, next) {
    this.cacheParamData();
    next();
  }
}
